/* src/components/Auth/AuthModal.css */
.auth-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* 어두운 반투명 배경 */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}

.auth-modal {
  background-color: #ffffff;     /* 흰색 배경 */
  color: #111111;                /* 검은 글자 */
  padding: 2rem;
  border-radius: 12px;
  width: 100%;
  max-width: 400px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.3);
}

.auth-modal h2 {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 1rem;
  color: #111111;
}

.auth-modal input {
  width: 100%;
  padding: 0.75rem;
  margin-bottom: 1rem;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 1rem;
}

.auth-modal button {
  width: 100%;
  padding: 0.75rem;
  background-color: #222222;
  color: #ffffff;
  border: none;
  border-radius: 6px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.2s;
}

.auth-modal button:hover {
  background-color: #444444;
}

.auth-modal p {
  margin-top: 1rem;
  font-size: 0.9rem;
  color: #333;
  text-align: center;
  cursor: pointer;
}

.close-button {
  position: absolute;
  top: 12px;
  right: 16px;
  font-size: 1.5rem;
  background: none;
  border: none;
  cursor: pointer;
}
